import React, { useState } from "react";
import styles from "./App.module.scss";
// import { nanoid } from "nanoid";
// import PropTypes from "prop-types";
import ProductList from "./product";

const PRODUCTS = [];

function List(props) {
   const [isedit, setedit] = useState(false);
   /*=====================Defined========================== */

   const productList = PRODUCTS.map((p) => <ProductList id={p.id} name={p.name} completed={p.completed} key={p.id} />);
   /*=====================Return========================== */
   return (
      <div className={styles.outer}>
         <div>{props.title}</div>

         {isedit ? <input className={styles.product} type="text" /> : <div></div>}
         <div className={styles.productList}>{productList}</div>
         <div className={styles.btn}>
            <button className={styles.addListBtn} onClick={AddCardHandler}>
               Add Card
            </button>
            <button className={styles.cancelBtn} onClick={uneditHandler}>
               Cancel
            </button>
         </div>
      </div>
   );
   /*=====================Event========================== */
   function AddCardHandler() {
      console.log("Second");
      setedit(true);
   }
   function uneditHandler() {
      if (!isedit) {
         setedit(true);
      }
   }
}

export default List;
